.last-run-task-status {
  width: 30px;
  height: 30px;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  background-color: rgba(56, 56, 70, 0.5);
  border-radius: 50%;
  margin-top: 4px;
  transition: color 0.25s ease, text-shadow 0.25s ease;
}
.last-run-task-status.last-run-task-status__danger {
  color: #F95F53;
}
.cf-resource-card__disabled .last-run-task-status.last-run-task-status__danger {
  color: #bf6362;
}
.last-run-task-status.last-run-task-status__danger.last-run-task-status__highlight {
  color: #FF8564;
  text-shadow: 0 0 4px #DC4E58;
}
.last-run-task-status.last-run-task-status__success {
  color: #4ED8A0;
}
.cf-resource-card__disabled .last-run-task-status.last-run-task-status__success {
  color: #58ac90;
}
.last-run-task-status.last-run-task-status__success.last-run-task-status__highlight {
  color: #7CE490;
  text-shadow: 0 0 4px #32B08C;
}
.last-run-task-status.last-run-task-status__warning {
  color: #FFB94A;
}
.cf-resource-card__disabled .last-run-task-status.last-run-task-status__warning {
  color: #c2995c;
}
.last-run-task-status.last-run-task-status__warning.last-run-task-status__highlight {
  color: #FFD255;
  text-shadow: 0 0 4px #E85B1C;
}

.last-run-task-status--popover .cf-popover--contents {
  max-width: 300px;
  padding: 8px 12px;
  font-size: 13px;
  font-family: "RobotoMono", monospace;
}
.last-run-task-status--popover .cf-popover--contents > h6 {
  margin-top: 0;
}
.last-run-task-status--popover .cf-popover--contents > p {
  margin-bottom: 0;
}